@keyframes blink {
  0% {
    opacity: 0.2;
  }

  20% {
    opacity: 1;
  }

  100% {
    opacity: 0.2;
  }
}

.saving {
  display: inherit;
  letter-spacing: 2px;
  font-weight: bold;
  padding: 0;
}

.saving span {
  animation-name: blink;

  animation-duration: 1.4s;

  animation-iteration-count: infinite;

  animation-fill-mode: both;
}

.saving span:nth-child(2) {
  animation-delay: 0.2s;
}

.saving span:nth-child(3) {
  animation-delay: 0.4s;
}
